.card {
    background-color: var(--cor-fundo-claro);
    border-radius: 12px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);
    flex: 1;
    text-align: center;
    overflow: hidden;
}

.content {
    margin: 0rem 2rem 2rem 2rem;
}

.card:hover {
    transform: translateY(-10px) scale(1.1);
    transition: transform 0.3s ease; 
}
  
.card .subtitle {
    font-size: 1.2rem;
    text-align: center;
    font-weight: 500;
    text-decoration: underline;
    text-underline-offset: 4px;
    text-decoration-color: var(--cor-secundaria-escura);
}
  
.card p {
    text-align: justify;
}
  
.card-icon {
    width: 40px;
    margin-top: 3rem;
}

.large-image {
    width: 100%;
    height: 300px;
    object-fit: cover;
    display: block;
    margin: 0;
    border-top-left-radius: 12px;
    border-top-right-radius: 12px;
}